import React from 'react';
import { useLocation } from 'react-router-dom';
import CodeEditor from './CodeEditor';

const defaultJSCode = `console.log("Hello, World!");

function greet(name) {
  return "Hello, " + name + "!";
}

console.log(greet("JavaScript"));`;

const JSCompiler = () => {
  const location = useLocation();
  const initialCode = location.state?.initialCode || defaultJSCode;

  return (
    <div>
      <h1>JavaScript Compiler</h1>
      <CodeEditor language="js" initialCode={initialCode} />
    </div>
  );
};

export default JSCompiler;
