import React from 'react';
import { useLocation } from 'react-router-dom';
import CodeEditor from './CodeEditor';

const defaultCSSCode = `body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
}

h1 {
  color: blue;
}

p {
  color: green;
}`;

const CSSCompiler = () => {
  const location = useLocation();
  const initialCode = location.state?.initialCode || defaultCSSCode;

  return (
    <div>
      <h1>CSS Compiler</h1>
      <CodeEditor language="css" initialCode={initialCode} />
    </div>
  );
};

export default CSSCompiler;
