import React from 'react';
import { useLocation } from 'react-router-dom';
import CodeEditor from './CodeEditor';

const defaultPythonCode = `print("Hello, World!")

def greet(name):
    return f"Hello, {name}!"

print(greet("Python"))`;

const PythonCompiler = () => {
  const location = useLocation();
  const initialCode = location.state?.initialCode || defaultPythonCode;

  return (
    <div>
      <h1>Python Compiler</h1>
      <CodeEditor language="python" initialCode={initialCode} />
    </div>
  );
};

export default PythonCompiler;
